import "../../../src/App.scss";
import { useParams, Link } from "react-router-dom";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { add, count, addNum } from "../../store.js";
import "bootstrap/dist/css/bootstrap.min.css";
import { useState, useEffect } from "react";

function Mist() {
  let state = useSelector((state) => {
    return state;
  });
  let { id } = useParams();
  let dispatch = useDispatch();

  let mist = state.mist.find(function (a) {
    return a.id == id;
  });

  let [num, setNum] = useState(1);
  let [cartmodal, setCartModal] = useState("");

  useEffect(() => {
    let getWatched = JSON.parse(localStorage.getItem("watched"));

    if (getWatched.some((i) => i.id === mist.id)) {
      getWatched = getWatched.filter((a) => a.id !== mist.id);
    }
    if (getWatched.length >= 3) {
      getWatched.shift();
    }
    getWatched.push(mist);
    localStorage.setItem("watched", JSON.stringify(getWatched));
  }, []);

  if (mist) {
    return (
      <section>
        <div className="perfume-container">
          <div className="perfume-div1">
            <img src={`${mist.img}`} />
          </div>
          <div className="perfume-div2">
            <div className="perfume-txt-div">
              <p className="perfume-name">{mist.name}</p>
              <p className="perfume-top">{mist.top}</p>
              <p className="perfume-middle">{mist.middle}</p>
              <p className="perfume-base">{mist.base}</p>
              <p className="perfume-price">
                {Number(mist.price).toLocaleString()}원
              </p>
            </div>
            <div className="perfume-count-div">
              <button
                onClick={() => {
                  if (num > 1) {
                    setNum(num - 1);
                  }
                }}
              >
                -
              </button>
              <p className="perfume-count">{num}</p>
              <button
                onClick={() => {
                  if (num > 0) {
                    setNum(num + 1);
                  }
                }}
              >
                +
              </button>
              <p className="perfume-total">
                총 : {(parseInt(mist.price) * num).toLocaleString()}원
              </p>
            </div>
            <div className="perfume-btn-div">
              <button
                onClick={() => {
                  let 장바구니 = state.cart.findIndex((a) => a.id === mist.id);
                  if (장바구니 === -1) {
                    dispatch(
                      add({
                        id: mist.id,
                        name: mist.name,
                        price: mist.price,
                        count: parseInt(mist.count) * num,
                        num: num,
                        img: mist.img,
                      })
                    );
                  } else {
                    dispatch(count({ id: mist.id, num: num }));
                  }
                  setCartModal("show");
                }}
              >
                Cart
              </button>
              <button
                onClick={() => {
                  alert("준비중 입니다.");
                }}
              >
                Buy Now
              </button>
            </div>
          </div>
        </div>
        <div className="accordion accordion-flush" id="accordionFlushExample">
          <div className="accordion-item">
            <h2 className="accordion-header">
              <button
                className="accordion-button collapsed"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#flush-collapseOne"
                aria-expanded="false"
                aria-controls="flush-collapseOne"
              >
                상품 정보 고시
              </button>
            </h2>
            <div
              id="flush-collapseOne"
              className="accordion-collapse collapse"
              data-bs-parent="#accordionFlushExample"
            >
              <div className="accordion-body">
                <p>상품 정보</p>

                <ul>
                  <li>상품명 : {mist.name}</li>
                  <li>용량(중량) : {mist.size}</li>
                  <li>제품 주요 사양 : All Skin Types</li>
                  <li>제품 성분 : {mist.ingredient}</li>
                  <li>사용기한 또는 개봉 후 사용기간: 제조 후 24개월</li>
                  <li>
                    사용방법: 맥박이 뛰는 피부나 옷 안쪽에 가볍게 분사하여
                    사용합니다.
                  </li>
                  <li>제조업자 및 제조판매업자: 쎙코(주)</li>
                  <li>제조국: 대한민국</li>
                  <li>식품의약품안전처 심사필 유무: 해당 사항 없음</li>
                  <li>
                    품질보증기준: 본 제품에 이상이 있을 경우, 공정거래위원회
                    고시에 의거 보상해 드립니다.
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div className="accordion-item">
            <h2 className="accordion-header">
              <button
                className="accordion-button collapsed"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#flush-collapseTwo"
                aria-expanded="false"
                aria-controls="flush-collapseTwo"
              >
                유의사항
              </button>
            </h2>
            <div
              id="flush-collapseTwo"
              className="accordion-collapse collapse"
              data-bs-parent="#accordionFlushExample"
            >
              <div className="accordion-body">
                <p>사용할 때의 주의사항</p>
                <ul>
                  <li>
                    가. 화장품 사용 시 또는 사용 후 직사광선에 의하여 사용부위가
                    붉은 반점, 부어오름 또는 가려움증 등의 이상 증상이나
                    부작용이 있는 경우에는 전문의 등과 상담할 것
                  </li>
                  <li>나. 상처가 있는 부위 등에는 사용을 자제할 것</li>
                  <li>
                    다. 보관 및 취급 시 주의사항
                    <ul>
                      <li>1. 어린이의 손이 닿지 않는 곳에 보관할 것</li>
                      <li>2. 직사광선을 피해서 보관할 것</li>
                    </ul>
                  </li>
                  <li>
                    본 제품에 이상이 있을 경우 공정거래위원회 고시
                    ‘소비자분쟁해결기준’에 의해 보상해 드립니다.
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        <CartModal cartmodal={cartmodal} setCartModal={setCartModal} />
      </section>
    );
  }
}

function CartModal({ cartmodal, setCartModal }) {
  return (
    <div className={`cartmodal-container ${cartmodal}`}>
      <div
        className="black-bg"
        onClick={() => {
          setCartModal("");
        }}
      ></div>
      <div className="cartmodal">
        <h3 className="cartmodal-header">장바구니담기</h3>
        <p className="cartmodal-main">상품을 장바구니에 담았습니다.</p>
        <div className="cartmodal-link">
          <Link
            onClick={() => {
              setCartModal("");
            }}
          >
            쇼핑 계속하기
          </Link>
          <Link
            to="/cart"
            onClick={() => {
              setCartModal("");
            }}
          >
            장바구니 이동
          </Link>
        </div>
      </div>
    </div>
  );
}

export default Mist;
